import React, { useState, useEffect } from 'react';
import {useDispatch, useSelector} from "react-redux";
import {addTask, selectTodo} from "./todoSlice";
import '../todo.css';
import { Form, Input, DatePicker, Button, Upload } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import serverApis from "../../ServerApis/serverApis";
import axios from "axios";


const  TodoForm = ()=> {
    //redux state
    const todoArr = useSelector(selectTodo);
    const dispatch = useDispatch();
    //component state
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [alertRequired, setAlert] = useState(false);
    const [dueDate, setDuedate] = useState('');
    const [attachment, setAttachment] = useState('');

    //antd from hook
    const [form] = Form.useForm();

    const isAlertRequired = (val)=> {
        setAlert(!alertRequired);
    }

    const handleTitleChange = (event) => {
        setTitle(event.target.value);
    }

    const handleContentChange = (event) => {
        setContent(event.target.value);
    }

    const handleDueDateChange = (value) =>{
        let date = value.format('ddd DD MMM YYYY HH:mm');
        setDuedate(date);
    }

    const handleAttachment = (e) => {
        let file = e.target.files[0];
        setAttachment(file);
    }

    const saveTask = () =>{
        let task = {'title': title, 'content': content, 'dueDate': dueDate, 'attachment': attachment};
        saveTaskOnServer(task);
        //redux state update
        //not required while using antd forms, since i'm clearing the forms after submit and i'm not allowing adding empty strings, the input fields are set to reuiqred
        setTitle("");
        setContent("");
    }

    const saveTaskOnServer = (task) =>{

        const formData = new FormData();

        // Update the formData object
        //formData.append("file",JSON.stringify(task.attachment.originFileObj));

        formData.append('myImage',task.attachment);
        const config = {
            headers: {
                'content-type': 'multipart/form-data'
            }
        };
        // axios.post("http://localhost:3001/todo/",formData,config)
        //     .then((response) => {
        //         alert("The file is successfully uploaded");
        //     }).catch((error) => {
        // });


        serverApis.post('/todo/',formData, {"Content-Type": "multipart/form-data"},(taskData)=> {dispatch(addTask(taskData.data))}, (err)=>{alert(err.message)});


        // serverApis.post('/todo/',{
        //          title: task.title,
        //          content: task.content,
        //          isCompleted: false,
        //          dueDate:task.dueDate,
        //          attachment: task.attachment
        //      }, {"Content-Type": "multipart/from-data"},(taskData)=> {dispatch(addTask(taskData.data))}, (err)=>{alert(err.message)});
    }

    const dummyRequest = ({file, onSuccess}) =>{
        setTimeout(() => {onSuccess("ok")},0);
    }

    const layout = {
        labelCol: { span: 4 },
        wrapperCol: { span: 16 },
    };

    const validateMessages = {
        required: '${label} is required!',
        types: {
            email: '${label} is not a valid email!',
            number: '${label} is not a valid number!',
        },
        number: {
            range: '${label} must be between ${min} and ${max}',
        },
    };

    const onFinish = (values) => {
        saveTask();
        form.resetFields();
    };



    return (
        <div className={'left_panel_content'}>
           <div className={'add_task_title'}>Add new Task</div>
            <Form {...layout} form={form} name="nest-messages"  onFinish={onFinish}  validateMessages={validateMessages}>
                <Form.Item label="Title" name={['todo', 'title']} rules={[{ required: true }]}>
                    <Input value={title} onChange={handleTitleChange} />
                </Form.Item>
                <Form.Item name={['todo', 'content']} label="Content" >
                    <Input.TextArea  value={content} onChange={handleContentChange}   />
                </Form.Item>
                <Form.Item label="Due Date">
                    <DatePicker onChange={handleDueDateChange} onOk={handleDueDateChange} showTime={{ format: 'HH:mm' }}
                                format="YYYY-MM-DD HH:mm"/>
                </Form.Item>
                <Form.Item label="Attachment">
                    {/*<Upload onChange={handleAttachment} customRequest={dummyRequest} >*/}
                    {/*    <Button icon={<UploadOutlined />}>Click to Upload</Button>*/}
                    {/*</Upload>*/}
                    <input type="file" onChange={handleAttachment} name="myAttachment"/>
                </Form.Item>
                <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 10 }}>
                    <Button type="primary" htmlType="submit"  >
                        Save Task
                    </Button>
                </Form.Item>
            </Form>
        </div>
    )
}

export default TodoForm;